require('./styles/site.less');
require('./vendor/scripts/timber.js');
require('./vendor/scripts/jquery.tm.avalanche.js');
require('./vendor/scripts/jquery.tm.counter.js');
require('./vendor/scripts/jquery.tm.equalize.js');
require('./vendor/scripts/jquery.tm.horizon.js');
require('./vendor/scripts/jquery.tm.retinize.js');
require('./vendor/scripts/jquery.tm.snowbridge.js');
require('./vendor/scripts/jquery.tm.summit.js');
require('./vendor/scripts/jquery.tm.swipe.js');

var core = require('@squarespace/core');

window.addEventListener('DOMContentLoaded', function() {

  var images = document.querySelectorAll('img[data-src]');

  for (var i = 0; i < images.length; i++) {
    core.ImageLoader.load(images[i], {
      load: true
    });
  }

});
